.login{
  margin: 100px 10px auto 100px;
  position: relative;
  align-items: center;
  float: inline-end;

}

form{
  text-align: center;
  align-items: center;
}
.google-button {
  height: 40px;
  border-width: 0;
  background: white;
  color: #737373;
  border-radius: 5px;
  white-space: nowrap;
  box-shadow: 1px 1px 0px 1px rgba(0,0,0,0.05);
  transition-property: background-color, box-shadow;
  transition-duration: 150ms;
  transition-timing-function: ease-in-out;
  padding: 0;
 cursor: pointer;

}

.google-button:hover{
  box-shadow: 1px 4px 5px 1px rgba(2, 0, 0, 0.1);
  background-color: #795548;
  color: #fff;
}
.google-button:active{
  background-color: #e5e5e5;
  box-shadow: none;
  transition-duration: 10ms;
}
    
.google-button__icon {
  display: inline-block;
  vertical-align: middle;
  margin: 8px 0 8px 8px;
  width: 18px;
  height: 18px;
  box-sizing: border-box;
}

.google-button__icon--plus {
  width: 27px;
}

.google-button__text {
  display: inline-block;
  vertical-align: middle;
  padding: 0 24px;
  font-size: 14px;
  font-weight: bold;
  font-family: 'Roboto',arial,sans-serif;
}
.button__text {
  display: inline-block;
  vertical-align: middle;
  padding: 0 24px;
  font-size: 14px;
  font-weight: bold;
  font-family: 'Roboto',arial,sans-serif;
}

button {
  margin-right: 10px;
 
}
.button{
  
  border-width: 0;
  background: white;
  color: #737373;
  border-radius: 50px;
  white-space: nowrap;
  box-shadow: 1px 1px 0px 1px #607d8b;
  transition-property: background-color, box-shadow;
  transition-duration: 150ms;
  transition-timing-function: ease-in-out;
  padding: 0;
 cursor: pointer;
}
.add{
  cursor: pointer;
  border-radius: 5px;
  white-space: nowrap;
  text-shadow:0px 0px 10px  #98bed1;
  transition-property: background-color, box-shadow;
  transition-duration: 150ms;
  transition-timing-function: ease-in-out;
  padding: 0;
}

button:focus{
  outline: none;
}

